<template>
  <div class="chat-container">
    <h1>AI 聊天机器人</h1>
    <div class="chat-box" ref="chatBox">
      <div
        v-for="(msg, index) in messages"
        :key="index"
        :class="['message', msg.sender]"
      >
        <strong>{{ msg.sender === "user" ? "你:" : "AI:" }}</strong>
        <p>{{ msg.text }}</p>
      </div>
      <!-- 流式响应的 AI 消息 -->
      <div v-if="isStreaming" class="message ai">
        <strong>AI:</strong>
        <p>{{ streamText }}</p>
      </div>
    </div>
    <div class="input-area">
      <input
        v-model="userInput"
        @keyup.enter="sendMessage"
        type="text"
        placeholder="输入消息..."
        :disabled="isStreaming"
      />
      <button @click="sendMessage" :disabled="!userInput.trim() || isStreaming">
        {{ isStreaming ? "发送中..." : "发送" }}
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "ChatView",
  data() {
    return {
      messages: [],
      userInput: "",
      isStreaming: false,
      streamText: "",
    };
  },
  methods: {
    async sendMessage() {
      const message = this.userInput.trim();
      if (!message || this.isStreaming) return;

      // 添加用户消息
      this.messages.push({ sender: "user", text: message });
      this.userInput = "";
      this.isStreaming = true;
      this.streamText = "";

      try {
        // 使用流式 API (/api/chat-stream)
        const response = await fetch("/api/chat-stream", {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify({ message: message }),
        });

        if (!response.ok) {
          throw new Error(`HTTP error! status: ${response.status}`);
        }

        const reader = response.body.getReader();
        const decoder = new TextDecoder();

        let shouldContinue = true;
        while (shouldContinue) {
          const { done, value } = await reader.read();
          if (done === true) break;

          const chunk = decoder.decode(value, { stream: true });
          // 简单处理：假设流返回的是纯文本片段
          this.streamText += chunk;
          // 滚动到底部
          this.$nextTick(() => {
            this.scrollToBottom();
          });
        }

        // 流结束，将完整响应添加到消息列表
        if (this.streamText) {
          this.messages.push({ sender: "ai", text: this.streamText });
        }
      } catch (error) {
        console.error("发送消息失败:", error);
        this.messages.push({ sender: "ai", text: "抱歉，AI 服务暂时不可用。" });
      } finally {
        this.isStreaming = false;
        this.streamText = "";
        this.scrollToBottom();
      }
    },
    scrollToBottom() {
      const container = this.$refs.chatBox;
      if (container) {
        container.scrollTop = container.scrollHeight;
      }
    },
  },
  mounted() {
    // 页面加载后自动滚动到底部
    this.scrollToBottom();
  },
};
</script>

